	export default {
		data() {
			return {
				login: getApp().globalData.isLogin
			};
		},
    onLoad() {
      console.log(this.login)
    },
    methods: {
      clickLogin() {
        this.login = getApp().globalData.isLogin = true
        console.log(this.login)
        uni.setStorage({
          key: 'isLogin',
          data: this.login
        })
      },
      clickQuit() {
        this.login = getApp().globalData.isLogin = false
        console.log(this.login)
        uni.setStorage({
          key: 'isLogin',
          data: this.login
        })
      }
    }
	}
